@tailwind base;
@tailwind components;
@tailwind utilities;

@import url('https://fonts.googleapis.com/css2?family=Inter:wght@300;400;500;600;700&display=swap');

@layer base {
  body {
    font-family: 'Inter', sans-serif;
    line-height: 1.5;
  }
}

@layer components {
  .btn-primary {
    @apply bg-black text-white px-6 py-3 font-medium tracking-wide transition-all duration-300 hover:bg-gray-800 focus:outline-none focus:ring-2 focus:ring-gray-500 focus:ring-offset-2;
  }
  
  .btn-secondary {
    @apply border border-black text-black px-6 py-3 font-medium tracking-wide transition-all duration-300 hover:bg-black hover:text-white focus:outline-none focus:ring-2 focus:ring-gray-500 focus:ring-offset-2;
  }
  
  .product-card {
    @apply cursor-pointer transition-all duration-300 hover:shadow-lg;
  }
  
  .product-image {
    @apply w-full h-64 object-cover transition-transform duration-300;
  }
  
  .nav-link {
    @apply text-sm font-medium tracking-wide text-gray-700 hover:text-black transition-colors duration-200;
  }
  
  /* Enhanced Floating Animations for Shoe */
  .animate-float {
    animation: float 6s ease-in-out infinite;
  }
  
  .animate-float-complex {
    animation: floatComplex 8s ease-in-out infinite;
  }
  
  .animate-bob {
    animation: bob 4s ease-in-out infinite;
  }
  
  .animate-sole-float {
    animation: soleFloat 6s ease-in-out infinite;
  }
  
  .animate-shimmer {
    animation: shimmer 3s ease-in-out infinite;
  }
  
  .animate-shimmer-delay {
    animation: shimmer 3s ease-in-out infinite 1.5s;
  }
  
  .animate-glow {
    animation: glow 2s ease-in-out infinite alternate;
  }
  
  .animate-pulse-slow {
    animation: pulseSlow 4s ease-in-out infinite;
  }
}

@layer utilities {
  /* Enhanced keyframe animations */
  @keyframes float {
    0%, 100% {
      transform: translateY(0px) rotate(12deg);
    }
    50% {
      transform: translateY(-20px) rotate(8deg);
    }
  }
  
  @keyframes floatComplex {
    0% {
      transform: translateY(0px) translateX(0px) scale(1);
    }
    25% {
      transform: translateY(-15px) translateX(5px) scale(1.02);
    }
    50% {
      transform: translateY(-25px) translateX(0px) scale(1.05);
    }
    75% {
      transform: translateY(-15px) translateX(-5px) scale(1.02);
    }
    100% {
      transform: translateY(0px) translateX(0px) scale(1);
    }
  }
  
  @keyframes bob {
    0%, 100% {
      transform: translateY(0px) rotate(12deg);
    }
    33% {
      transform: translateY(-8px) rotate(10deg);
    }
    66% {
      transform: translateY(4px) rotate(14deg);
    }
  }
  
  @keyframes soleFloat {
    0%, 100% {
      transform: translateX(-50%) translateY(0px) scaleX(1);
    }
    50% {
      transform: translateX(-50%) translateY(-8px) scaleX(1.1);
    }
  }
  
  @keyframes shimmer {
    0%, 100% {
      opacity: 0.6;
      transform: scale(1) rotate(0deg);
    }
    50% {
      opacity: 0.9;
      transform: scale(1.1) rotate(5deg);
    }
  }
  
  @keyframes glow {
    0% {
      opacity: 0.7;
      box-shadow: 0 0 20px rgba(34, 211, 238, 0.4);
    }
    100% {
      opacity: 1;
      box-shadow: 0 0 30px rgba(34, 211, 238, 0.8);
    }
  }
  
  @keyframes pulseSlow {
    0%, 100% {
      opacity: 0.3;
      transform: scale(1);
    }
    50% {
      opacity: 0.6;
      transform: scale(1.1);
    }
  }
  
  /* Gradient utilities */
  .bg-gradient-radial {
    background: radial-gradient(ellipse at center, var(--tw-gradient-stops));
  }
  
  .perspective-1000 {
    perspective: 1000px;
  }
  
  .rotateX-10 {
    transform: rotateX(10deg);
  }
}